.categories {
  &__wrapper {
    display: grid;
    row-gap: 40px;

    @include on-tablet {
      row-gap: 64px;
    }
  }
}

.category {
  &__photos {
   @include page-grid;

   margin-bottom: 16px;
  }

  &__link {
    position: relative;

    &--wide {
    @include on-tablet {
     grid-column: span 4;
    }

    @include on-desktop {
     grid-column: span 8;
    }
  }
    &--square {
      height: 0;
      padding-bottom: 100%;

      @include on-tablet {
      grid-column: span 2;

    }

    @include on-desktop {
      grid-column: span 4;
    }
  }
}
  &__photo {
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;

    @include hover(transform, scale(1.05))
  }
  &__title {
    margin: 0;
    font-weight: 700;
    line-height: 140%;
    font-size: 18px;
  }
}
