.product {

&__photo {
  margin-bottom: 24px;
  width: 100%;

  @include hover(transform, scale(1.1));
}

&__title {
  margin-bottom: 8px;
  font-size: 18px;
  font-family: $main-font-family;
  font-weight: $main-font-weight;
}

&__description {
  margin: 0;
  margin-bottom: 12px;
  font-size: 16px;
  line-height: 140%;
  font-family: $secondary-font-family;
  font-weight: $secondary-font-weight;
  color: $main-text-color;

  &--hidden {
    @include on-desktop{
      display: none;
    }
  }

  @include on-desktop {
    max-width: 280px;
    height: 88px;
  }
}

&__price {
  margin: 0;
  font-size: 16px;
  line-height: 140%;
  font-weight: $secondary-font-weight;
  color: $main-text-color;
 }
}
